<template>
  <div id="app">
    <h1 is="sui-header">무트코인</h1>
    <div id="priceContainer">
      <sui-input v-model.number="base" id="base" placeholder="구매가" /><br>
      <sui-input v-model.number="price_0" id="price_0" placeholder="월요일 오전" />
      <sui-input v-model.number="price_1" id="price_1" placeholder="월요일 오후" /><br>

      <sui-input v-model.number="price_2" id="price_2" placeholder="화요일 오전" />
      <sui-input v-model.number="price_3" id="price_3" placeholder="화요일 오후" /><br>

      <sui-input v-model.number="price_4" id="price_4" placeholder="수요일 오전" />
      <sui-input v-model.number="price_5" id="price_5" placeholder="수요일 오후" /><br>

      <sui-input v-model.number="price_6" id="price_6" placeholder="목요일 오전" />
      <sui-input v-model.number="price_7" id="price_7" placeholder="목요일 오후" /><br>

      <sui-input v-model.number="price_8" id="price_8" placeholder="금요일 오전" />
      <sui-input v-model.number="price_9" id="price_9" placeholder="금요일 오후" /><br>

      <sui-input v-model.number="price_10" id="price_10" placeholder="토요일 오전" />
      <sui-input v-model.number="price_11" id="price_11" placeholder="토요일 오후" /><br>
    </div>
    <sui-button id="calcbutton" @click="calc()">{{status == 'idle' ? '가즈아!!' : `계산중...(${(100 - (num / LOOP * 100)).toFixed(2)}%)`}}</sui-button>

    <div id="result">
      <template v-for="text in result">
        <template v-if="typeof text === 'string'">
          <h2 is="sui-header">{{text}}</h2>
        </template>
        <template v-else-if="text[0] >= lastday">
  <sui-table>
    <sui-table-body>
      <sui-table-row>
        <sui-table-cell style="width:100px; text-align:right;">{{text[1]}} 벨</sui-table-cell>
        <sui-table-cell style="text-align:right;">
          <template v-if="text[2] > 20">
            <div class="red">{{text[2].toFixed(2)}}%</div>
          </template>
          <template v-else-if="text[2] > 5">
            <div class="red2">{{text[2].toFixed(2)}}%</div>
          </template>
          <template v-else>
            {{text[2].toFixed(2)}}%
          </template>
        </sui-table-cell>
      </sui-table-row>
    </sui-table-body>
  </sui-table>
        </template>
      </template>
    </div>
  </div>
</template>

<script>
var LOOP = 0xfffff * 2;
import HelloWorld from './components/HelloWorld.vue'

function toJS(obj){
  let ret = new Object()
  ret.basePrice = obj.basePrice;
  ret.sellPrice_0 = obj.sellPrice_0;
  ret.sellPrice_1 = obj.sellPrice_1;
  ret.sellPrice_2 = obj.sellPrice_2;
  ret.sellPrice_3 = obj.sellPrice_3;
  ret.sellPrice_4 = obj.sellPrice_4;
  ret.sellPrice_5 = obj.sellPrice_5;
  ret.sellPrice_6 = obj.sellPrice_6;
  ret.sellPrice_7 = obj.sellPrice_7;
  ret.sellPrice_8 = obj.sellPrice_8;
  ret.sellPrice_9 = obj.sellPrice_9;
  ret.sellPrice_10 = obj.sellPrice_10;
  ret.sellPrice_11 = obj.sellPrice_11;

  return ret;
}

var workpool = new Array();
var wow = new Array();
var allNumber = 0;
export default {
  name: 'App',
  components: {
    HelloWorld
  },
  data: function(){
    return {
      LOOP: LOOP,
      status: "idle",
      num: 0,
      base: "",
      price_0: "",
      price_1: "",
      price_2: "",
      price_3: "",
      price_4: "",
      price_5: "",
      price_6: "",
      price_7: "",
      price_8: "",
      price_9: "",
      price_10: "",
      price_11: "",
      result: [],
      lastday: -1,
    }
  },
  methods:{
    calc: function(){
      this.result = []
      var inputcheck = 0;
      this.lastday = -1;
      inputcheck += (typeof this.base === "number") | 0
      inputcheck += (typeof this.price_0 === "number") | 0
      inputcheck += (typeof this.price_1 === "number") | 0
      inputcheck += (typeof this.price_2 === "number") | 0
      inputcheck += (typeof this.price_3 === "number") | 0
      inputcheck += (typeof this.price_4 === "number") | 0
      inputcheck += (typeof this.price_5 === "number") | 0
      inputcheck += (typeof this.price_6 === "number") | 0
      inputcheck += (typeof this.price_7 === "number") | 0
      inputcheck += (typeof this.price_8 === "number") | 0
      inputcheck += (typeof this.price_9 === "number") | 0
      inputcheck += (typeof this.price_10 === "number") | 0
      inputcheck += (typeof this.price_11 === "number") | 0

      if(typeof this.price_0 === "number") this.lastday = 0
      if(typeof this.price_1 === "number") this.lastday = 1
      if(typeof this.price_2 === "number") this.lastday = 2
      if(typeof this.price_3 === "number") this.lastday = 3
      if(typeof this.price_4 === "number") this.lastday = 4
      if(typeof this.price_5 === "number") this.lastday = 5
      if(typeof this.price_6 === "number") this.lastday = 6
      if(typeof this.price_7 === "number") this.lastday = 7
      if(typeof this.price_8 === "number") this.lastday = 8
      if(typeof this.price_9 === "number") this.lastday = 9
      if(typeof this.price_10 === "number") this.lastday = 10
      if(typeof this.price_11 === "number") this.lastday = 11

      if(inputcheck < 2){
        this.result.push("적어도 두개 이상의 값을 입력해 주세요.")
        return;
      }
      this.status = 'running'
      allNumber = 0;
      workpool = [];
      wow = [];
      // for (let seed = 0; seed < LOOP; seed++) {
      //   workpool.push(seed)
      // }
      workpool = Array.from({length: LOOP}, () => Math.floor(Math.random() * 0xffffffff));

      var fun = (taskStartTime) => {
        var taskFinishTime;
        do{
          var seed = workpool.pop();
          if(seed === undefined){
            break;
          }
          this.num = workpool.length;
          for (let type = 0; type < 5; type++) {
            var q = Module.calc(type, seed);
            if(typeof this.base === "number" && this.base != 0 && Math.abs(q.basePrice - this.base) > 1) {q.delete(); continue;}
            if(typeof this.price_0 === "number" && this.price_0 != 0 && Math.abs(q.sellPrice_0 - this.price_0) > 1) {q.delete(); continue;}
            if(typeof this.price_1 === "number" && this.price_1 != 0 && Math.abs(q.sellPrice_1 - this.price_1) > 1) {q.delete(); continue;}
            if(typeof this.price_2 === "number" && this.price_2 != 0 && Math.abs(q.sellPrice_2 - this.price_2) > 1) {q.delete(); continue;}
            if(typeof this.price_3 === "number" && this.price_3 != 0 && Math.abs(q.sellPrice_3 - this.price_3) > 1) {q.delete(); continue;}
            if(typeof this.price_4 === "number" && this.price_4 != 0 && Math.abs(q.sellPrice_4 - this.price_4) > 1) {q.delete(); continue;}
            if(typeof this.price_5 === "number" && this.price_5 != 0 && Math.abs(q.sellPrice_5 - this.price_5) > 1) {q.delete(); continue;}
            if(typeof this.price_6 === "number" && this.price_6 != 0 && Math.abs(q.sellPrice_6 - this.price_6) > 1) {q.delete(); continue;}
            if(typeof this.price_7 === "number" && this.price_7 != 0 && Math.abs(q.sellPrice_7 - this.price_7) > 1) {q.delete(); continue;}
            if(typeof this.price_8 === "number" && this.price_8 != 0 && Math.abs(q.sellPrice_8 - this.price_8) > 1) {q.delete(); continue;}
            if(typeof this.price_9 === "number" && this.price_9 != 0 && Math.abs(q.sellPrice_9 - this.price_9) > 1) {q.delete(); continue;}
            if(typeof this.price_10 === "number" && this.price_10 != 0 && Math.abs(q.sellPrice_10 - this.price_10) > 1) {q.delete(); continue;}
            if(typeof this.price_11 === "number" && this.price_11 != 0 && Math.abs(q.sellPrice_11 - this.price_11) > 1) {q.delete(); continue;}
            allNumber += 1;
            wow.push(toJS(q))
            q.delete();
          }
          taskFinishTime = window.performance.now();
        } while (taskFinishTime - taskStartTime < 50);
        if (workpool.length > 0)
          requestAnimationFrame(fun);
        else{
          console.log('end!')
          this.status = 'idle'
          console.log(allNumber)
          var save = new Object();
          function init(){
            var ret = new Array();
            for (let index = 0; index < 1000; index++) {
              ret.push(0);
            }

            return ret;
          }
          save.basePrice = init();
          save.sellPrice_0 = init();
          save.sellPrice_1 = init();
          save.sellPrice_2 = init();
          save.sellPrice_3 = init();
          save.sellPrice_4 = init();
          save.sellPrice_5 = init();
          save.sellPrice_6 = init();
          save.sellPrice_7 = init();
          save.sellPrice_8 = init();
          save.sellPrice_9 = init();
          save.sellPrice_10 = init();
          save.sellPrice_11 = init();
          wow.forEach(element => {
            save.basePrice[element.basePrice] += 1
            save.sellPrice_0[element.sellPrice_0] += 1
            save.sellPrice_1[element.sellPrice_1] += 1
            save.sellPrice_2[element.sellPrice_2] += 1
            save.sellPrice_3[element.sellPrice_3] += 1
            save.sellPrice_4[element.sellPrice_4] += 1
            save.sellPrice_5[element.sellPrice_5] += 1
            save.sellPrice_6[element.sellPrice_6] += 1
            save.sellPrice_7[element.sellPrice_7] += 1
            save.sellPrice_8[element.sellPrice_8] += 1
            save.sellPrice_9[element.sellPrice_9] += 1
            save.sellPrice_10[element.sellPrice_10] += 1
            save.sellPrice_11[element.sellPrice_11] += 1
          });
          save.basePrice = save.basePrice.map(x => x / allNumber * 100);
          save.sellPrice_0 = save.sellPrice_0.map(x => x / allNumber * 100);
          save.sellPrice_1 = save.sellPrice_1.map(x => x / allNumber * 100);
          save.sellPrice_2 = save.sellPrice_2.map(x => x / allNumber * 100);
          save.sellPrice_3 = save.sellPrice_3.map(x => x / allNumber * 100);
          save.sellPrice_4 = save.sellPrice_4.map(x => x / allNumber * 100);
          save.sellPrice_5 = save.sellPrice_5.map(x => x / allNumber * 100);
          save.sellPrice_6 = save.sellPrice_6.map(x => x / allNumber * 100);
          save.sellPrice_7 = save.sellPrice_7.map(x => x / allNumber * 100);
          save.sellPrice_8 = save.sellPrice_8.map(x => x / allNumber * 100);
          save.sellPrice_9 = save.sellPrice_9.map(x => x / allNumber * 100);
          save.sellPrice_10 = save.sellPrice_10.map(x => x / allNumber * 100);
          save.sellPrice_11 = save.sellPrice_11.map(x => x / allNumber * 100);
          // this.result.push("구매가")
          // for (let index = 0; index < save.basePrice.length; index++) {
          //   const percent = save.basePrice[index];
          //   if(percent > 0.2) this.result.push([0 * 2 + 0, index, percent])
          // }

          this.result.push("월요일 오전")
          for (let index = 0; index < save.sellPrice_0.length; index++) {
            const percent = save.sellPrice_0[index];
            if(percent > 0.2) this.result.push([0 * 2 + 0, index, percent])
          }

          this.result.push("월요일 오후")
          for (let index = 0; index < save.sellPrice_1.length; index++) {
            const percent = save.sellPrice_1[index];
            if(percent > 0.2) this.result.push([0 * 2 + 1, index, percent])
          }

          this.result.push("화요일 오전")
          for (let index = 0; index < save.sellPrice_2.length; index++) {
            const percent = save.sellPrice_2[index];
            if(percent > 0.2) this.result.push([1 * 2 + 0, index, percent])
          }

          this.result.push("화요일 오후")
          for (let index = 0; index < save.sellPrice_3.length; index++) {
            const percent = save.sellPrice_3[index];
            if(percent > 0.2) this.result.push([1 * 2 + 1, index, percent])
          }

          this.result.push("수요일 오전")
          for (let index = 0; index < save.sellPrice_4.length; index++) {
            const percent = save.sellPrice_4[index];
            if(percent > 0.2) this.result.push([2 * 2 + 0, index, percent])
          }

          this.result.push("수요일 오후")
          for (let index = 0; index < save.sellPrice_5.length; index++) {
            const percent = save.sellPrice_5[index];
            if(percent > 0.2) this.result.push([2 * 2 + 1, index, percent])
          }

          this.result.push("목요일 오전")
          for (let index = 0; index < save.sellPrice_6.length; index++) {
            const percent = save.sellPrice_6[index];
            if(percent > 0.2) this.result.push([3 * 2 + 0, index, percent])
          }

          this.result.push("목요일 오후")
          for (let index = 0; index < save.sellPrice_7.length; index++) {
            const percent = save.sellPrice_7[index];
            if(percent > 0.2) this.result.push([3 * 2 + 1, index, percent])
          }

          this.result.push("금요일 오전")
          for (let index = 0; index < save.sellPrice_8.length; index++) {
            const percent = save.sellPrice_8[index];
            if(percent > 0.2) this.result.push([4 * 2 + 0, index, percent])
          }

          this.result.push("금요일 오후")
          for (let index = 0; index < save.sellPrice_9.length; index++) {
            const percent = save.sellPrice_9[index];
            if(percent > 0.2) this.result.push([4 * 2 + 1, index, percent])
          }

          this.result.push("토요일 오전")
          for (let index = 0; index < save.sellPrice_10.length; index++) {
            const percent = save.sellPrice_10[index];
            if(percent > 0.2) this.result.push([5 * 2 + 0, index, percent])
          }

          this.result.push("토요일 오후")
          for (let index = 0; index < save.sellPrice_11.length; index++) {
            const percent = save.sellPrice_11[index];
            if(percent > 0.2) this.result.push([5 * 2 + 1, index, percent])
          }
        }
      }
      requestAnimationFrame(fun);
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
input::placeholder{
  color:#2185d0 !important;
}
#priceContainer input{
  margin: 10px;
}
#result{
  width: 200px;
  margin: 30px auto;
  text-align: left;
}
.ui.table{
  margin: 0.5em !important;
}
#calcbutton{
  margin: 30px;
}
.red{
  color:red;
  font-weight: 900;
}
.red2{
  color:red;
}
</style>
